.card__settings {
  margin-bottom: 10px;
}

.card__settings:hover {
  background-color: #edeff0;
}

.card__padding {
  padding: 5px 10px;
}

.card__text {
  word-wrap: break-word;
  margin: 0 0 5px;
}

.card__settings:hover .card__edit-pen {
  opacity: 1;
}

.card__header {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.card__color {
  position: relative;
  top: 8px;
  display: inline-block;
  width: 15px;
  height: 5px;
  vertical-align: top;
  background-color: #3aa6eb;
  border-radius: 5px;
}

.card__edit {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.card__edit-pen {
  display: inline-block;
  /*position: absolute;*/
  top: 10px;
  right: 10px;
  width: 18px;
  height: 18px;
  opacity: 0;
  background: url("data:image/svg+xml;charset=utf-8,%3C?xml version='1.0' encoding='utf-8'?%3E %3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 24 24' style='enable-background:new 0 0 24 24;' xml:space='preserve' fill='%23999'%3E %3Cpath d='M21.4,6L16,0.6l-14,14V20h5.4L21.4,6z M4,18v-2.6l12-12L18.6,6l-12,12H4z '/%3E %3C/svg%3E");
  background-size: 18px 18px;
}

.card__edit-pen:hover {
  background: url("data:image/svg+xml;charset=utf-8,%3C?xml version='1.0' encoding='utf-8'?%3E %3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 24 24' style='enable-background:new 0 0 24 24;' xml:space='preserve' fill='%234d4d4d'%3E %3Cpath d='M21.4,6L16,0.6l-14,14V20h5.4L21.4,6z M4,18v-2.6l12-12L18.6,6l-12,12H4z '/%3E %3C/svg%3E");
}

.card__dropdown {
  position: absolute;
  top: -8px;
  right: -150px;
  display: block;
  min-width: 140px;
  padding: 5px;
  /*margin: 2px 0 0;*/
  border: 1px solid #ccc;
  background-color: #fff;
  z-index: 1000;
  list-style: none;
  border-radius: 4px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
}

.card__list {
  padding: 4px 8px;
  color: #333;
  cursor: pointer;
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
  -webkit-font-smoothing: antialiased;
}

.card__list:hover {
  background-color: #555;
  color: #eee;
}

.card__owner {
  float: right;
}

.card__owner-avatar {
  height: 30px;
  width: 30px;
  border-radius: 3px;
}
